import React, { lazy, Suspense, PureComponent } from 'react';
import { getWallID } from './utils'
const Route = lazy(() => import('./Route'));

class Main extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            wall_id: getWallID()
        }
    }
    render() {
        const { wall_id } = this.state;
        console.log(wall_id)
        return <Suspense fallback={<></>}><Route wallID={wall_id} /></Suspense>
    }
}

export default Main;